header {
    height: 70px;
    background-color: #A9C9FF;
    background-image: linear-gradient(180deg, #A9C9FF 0%, #FFBBEC 100%);    
    h1 {
        margin-inline-start: 2rem;
        color: black;
    }
    nav a:last-child {
        margin-inline-end: 2rem;
    }

    nav a {
        margin-inline-start: 2rem;
        color: black;
        text-decoration: none;
        &.active{
            text-decoration: overline;
        }
    }
}